.switch {
            position: relative;
            display: inline-block;
            width: 4rem;
            height: 2rem;
            background-color: white;
            border-radius: 2rem;
            transition: all 0.3s linear;
        }

        .switch::after {
            content: "";
            position: absolute;
            top: .1rem;
            left: .1rem;
            width: 1.8rem;
            height: 1.8rem;
            border-radius: 50%;
            background-color: #3dc6c1;
            transition: all 0.3s linear;
        }


        input[type="checkbox"]:checked + .switch:after {
            translate: 2rem 0;
        }

        input[type="checkbox"]:checked + .switch {
            background-color: #2e3241;
        }

        input[type="checkbox"] {
            display: none;
        }